body,
html,
#root {
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
	width: 100%;
	height: 100%;
	background-color: rgb(12, 12, 40);
	overflow-y: hidden;
}

.no-input-warning {
	display: flex;
	position: fixed;
	width: 100%;
	justify-content: center;
	top: 25px;
	transition: opacity 0.5s;
	pointer-events: none;
}

.no-input-warning-content {
	padding: 15px 25px;
	border-radius: 15px;
	background: rgba(255, 255, 255, 0.5);
	text-align: center;
}

.camera-controls {
	position: fixed;
	bottom: 25px;
	left: 50%;
	transform: translateX(-50%);
	display: flex;
	justify-content: center;
	align-items: center;
}

.photo {
	display: inline-block;
	max-height: 20vh;
	padding: 10px;
	background-color: whitesmoke;
	cursor: pointer;
}

.photo-roll {
	display: flex;
	justify-content: center;
	align-items: center;
	height: auto;
	overflow-y: hidden;
	overflow-x: scroll;
	left: 25;
	right: 25;
	position: fixed;
}

.photo-book {
	top: 0;
	position: fixed;
	justify-content: center;
	align-items: center;
}